export async function pushRemoteContent(contentDynamic: any) {
  const res = await fetch(
    "https://curly-pond-edc4.snowy-frost-b196.workers.dev/api/remote-content",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ contentDynamic }),
    }
  );

  const data = await res.json();

  if (!res.ok) {
    throw new Error(data?.error || "Failed to push remote content");
  }

  return data;
}
