import React, { useMemo, useState } from "react";
import { pushRemoteContent } from "./remoteContent";
import { exampleData } from "./remoteConfigExampleData";

export default function TestRemoteContent() {


  const [jsonText, setJsonText] = useState(() =>
    JSON.stringify(exampleData, null, 2)
  );

  const [consoleLines, setConsoleLines] = useState<string[]>([
    "Ready. Edit JSON and click Submit.",
  ]);

  const [isSubmitting, setIsSubmitting] = useState(false);

  const log = (line: string) => {
    setConsoleLines((prev) => [
      `[${new Date().toLocaleTimeString()}] ${line}`,
      ...prev,
    ]);
  };

  const handleSubmit = async () => {
    if (isSubmitting) return;

    let parsed: any;
    try {
      parsed = JSON.parse(jsonText);
    } catch (err: any) {
      log(`‚ùå Invalid JSON: ${err?.message || String(err)}`);
      return;
    }

    try {
      setIsSubmitting(true);
      log("‚è≥ Submitting update...");

      const result : any = await pushRemoteContent(parsed);

      log("‚úÖ Update successful!");
      log(`newSha: ${result?.newSha || "unknown"}`);
      log(`usedFallback: ${String(result?.usedFallback ?? false)}`);
    } catch (err: any) {
      log(`‚ùå Update failed: ${err?.message || String(err)}`);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div style={{ maxWidth: 900, margin: "0 auto", padding: 16 }}>
      <h2 style={{ fontSize: 20, fontWeight: 700, marginBottom: 10 }}>
        TestRemoteContent
      </h2>

      <p style={{ marginBottom: 10, opacity: 0.8 }}>
        Edit the JSON below and submit it to your Worker endpoint.
      </p>

      <div
        style={{
          border: "1px solid rgba(255,255,255,0.12)",
          borderRadius: 12,
          padding: 12,
          background: "rgba(0,0,0,0.25)",
        }}
      >
        <label style={{ display: "block", marginBottom: 8, fontWeight: 600 }}>
          JSON Payload (contentDynamic)
        </label>

        <textarea
          value={jsonText}
          onChange={(e) => setJsonText(e.target.value)}
          spellCheck={false}
          style={{
            width: "100%",
            minHeight: 700,
            resize: "vertical",
            fontFamily: "ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, monospace",
            fontSize: 13,
            padding: 12,
            borderRadius: 10,
            border: "1px solid rgba(255,255,255,0.12)",
            background: "rgba(0,0,0,0.35)",
            color: "white",
            outline: "none",
          }}
        />

        <div style={{ display: "flex", gap: 10, marginTop: 12 }}>
          <button
            onClick={handleSubmit}
            disabled={isSubmitting}
            style={{
              padding: "10px 14px",
              borderRadius: 10,
              border: "1px solid rgba(255,255,255,0.18)",
              background: isSubmitting ? "rgba(255,255,255,0.08)" : "rgba(255,255,255,0.12)",
              color: "white",
              cursor: isSubmitting ? "not-allowed" : "pointer",
              fontWeight: 700,
            }}
          >
            {isSubmitting ? "Submitting..." : "Submit"}
          </button>

          <button
            onClick={() => {
              setJsonText(JSON.stringify(exampleData, null, 2));
              log("‚Ü©Ô∏è Reset JSON to exampleData");
            }}
            disabled={isSubmitting}
            style={{
              padding: "10px 14px",
              borderRadius: 10,
              border: "1px solid rgba(255,255,255,0.18)",
              background: "rgba(255,255,255,0.06)",
              color: "white",
              cursor: isSubmitting ? "not-allowed" : "pointer",
              fontWeight: 600,
            }}
          >
            Reset
          </button>

          <button
            onClick={() => {
              navigator.clipboard.writeText(jsonText);
              log("üìã Copied JSON to clipboard");
            }}
            disabled={isSubmitting}
            style={{
              padding: "10px 14px",
              borderRadius: 10,
              border: "1px solid rgba(255,255,255,0.18)",
              background: "rgba(255,255,255,0.06)",
              color: "white",
              cursor: isSubmitting ? "not-allowed" : "pointer",
              fontWeight: 600,
            }}
          >
            Copy
          </button>
        </div>
      </div>

      <div
        style={{
          marginTop: 16,
          border: "1px solid rgba(255,255,255,0.12)",
          borderRadius: 12,
          padding: 12,
          background: "rgba(0,0,0,0.25)",
        }}
      >
        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
          <h3 style={{ margin: 0, fontSize: 16, fontWeight: 700 }}>
            Console
          </h3>

          <button
            onClick={() => setConsoleLines(["Console cleared."])}
            style={{
              padding: "6px 10px",
              borderRadius: 10,
              border: "1px solid rgba(255,255,255,0.18)",
              background: "rgba(255,255,255,0.06)",
              color: "white",
              cursor: "pointer",
              fontWeight: 600,
              fontSize: 12,
            }}
          >
            Clear
          </button>
        </div>

        <div
          style={{
            marginTop: 10,
            width: "100%",
            minHeight: 140,
            maxHeight: 240,
            overflow: "auto",
            padding: 12,
            borderRadius: 10,
            border: "1px solid rgba(255,255,255,0.12)",
            background: "rgba(0,0,0,0.35)",
            color: "white",
            fontFamily: "ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, monospace",
            fontSize: 12,
            lineHeight: 1.4,
            whiteSpace: "pre-wrap",
          }}
        >
          {consoleLines.join("\n")}
        </div>
      </div>
    </div>
  );
}
